.block-job-title {
    font-size: 38px;
    font-weight: 300;
    margin: 0.8em 0;
    text-shadow: 0 1px 3px rgba($black, 0.16);
    .job-no {
        font-size: 36.8%;
        margin-left: 0.9em;
    }
}

.job-detail-wrapper {
    color: #707070;
    .form-control {
        font-size: 11px;
    }
    .status-value {
        font-weight: 700;
        option {
            font-weight: 700;
        }
    }
}

.image-input {
    margin-bottom: 1.5rem;
}

.detail-controls {
    align-items: center;
    &.on-footer {
        margin-top: 15px;
        margin-bottom: 15px;
    }
    .extra-controls {
        // margin-left: auto;
        li {
            &:not(:last-child) {
                margin-right: 27px;
            }
        }
    }
    .controls-info-label {
        margin-left: auto;
    }
}

.detail-group-controls {
    margin-top: 50px;
    padding-bottom: 100px;
}

.job-image-card {
    border-radius: 5px;
    border: solid 1px $light-grey;
    background-color: $white;
    position: relative;
    overflow: hidden;
    &:not(:last-child) {
        margin-bottom: 15px;
    }
    .remove-card {
        font-size: 12px;
        color: #B42500;
        display: flex;
        width: 70px;
        height: 30px;
        justify-content: center;
        align-items: center;
        position: absolute;
        right: 0;
        top: 0;
        cursor: pointer;
        &:hover {
            text-decoration: underline;
        }
    }
    .input-file-name {
        border-bottom: solid 1px $light-grey;
        align-items: center;
        margin-bottom: 0;
        dt {
            padding: 0.3em 1em;
            min-width: 120px;
            color: $white;
            background-color: $primary;
            text-align: center;
            text-shadow: 0 2px 3px rgba($black, 0.3);
            font-weight: 400;
            font-size: 14px;
            align-self: stretch;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: -1px 0 0 -1px;
            border-top-left-radius: 5px;
            &.video {
                background-color: $orange;
            }
        }
        dd {
            padding: 0.3em 1em;
            flex-grow: 1;
            font-size: 14px;
            margin: 0;
            text-decoration: underline;
        }
        &.size-small {
            dt {
                min-width: 100px;
            }
        }
    }
}

.job-card-preview {
    .inputs-content {
        width: 100%;
        flex-grow: 1;
        padding: 5px 15px;
    }
    .preview-input {
        width: 100px;
    }
}

.preview-input {
    width: 120px;
    align-self: stretch;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-shrink: 0;
    margin-left: -1px;
    // border-bottom-left-radius: 5px;
    // overflow: hidden;
    video {
        background-color: $light-grey;
        max-width: 100%;
        max-height: 100%;
        flex-shrink: 0;
    }
    .wrap-img {
        display: block;
        width: 100%;
        padding-bottom: 100%;
        position: relative;
        overflow: hidden;
        img {
            flex-shrink: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            position: absolute;
            z-index: 1;
        }
        .progress-block {
            position: absolute;
            z-index: 2;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 5px 5px 20px;
            color: #008015;
            background: rgba($white, 0.8);
            .progress-title {
                font-size: 12px;
                font-weight: 600;
                margin-bottom: 0.3em;
            }
            .progress {
                width: 100%;
                height: 10px;
                background-color: rgba($white, 0.5);
                border-radius: 3px;
                .progress-bar {
                    background-color: #008015;
                }
            }
        }
    }
}

.image-editor {
    width: 124px;
    max-width: 100%;
    border-right: solid 1px $light-grey;
    padding-right: 15px;
    margin-right: 15px;
    flex-shrink: 0;
    .image-pane {
        position: relative;
        .wrap-img {
            position: relative;
            padding-bottom: 100%;
            overflow: hidden;
            display: block;
            transition: all 0.2s;
            img {
                position: absolute;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            &[href]:hover {
                opacity: 0.8;
            }
            &[data-src],
            &[data-html] {
                transition: all 0.2s;
                cursor: pointer;
                &:hover {
                    opacity: 0.8;
                }
            }
        }
        .pane-header {
            align-items: center;
            margin-top: 5px;
        }
        .type {
            font-size: 12px;
            margin-bottom: 3px;
            font-weight: 700;
        }
        &:not(:last-child) {
            .image-input {
                position: relative;
                &:after {
                    content: "";
                    width: 1px;
                    height: 100%;
                    border-right: solid 1px $light-grey;
                    position: absolute;
                    right: -15px;
                    top: 0;
                }
            }
        }
        .image-input {
            .image-preview {
                width: 100%;
                height: 156px;
                display: flex;
                justify-content: center;
                align-items: center;
                img {
                    max-width: 100%;
                    max-height: 100%;
                    flex-shrink: 0;
                }
            }
            .preview-input {
                margin-left: 0;
                width: 85px;
            }
            .custom-input-file-label {
                height: 108px;
                font-size: 11px;
                padding: 10px;
                line-height: 1.3em;
                margin-bottom: 0;
            }
        }
        .form-check-label {
            &:before {
                top: 0.3em;
            }
            &:after {
                top: 0.55em;
            }
        }
        .deleted {
            cursor: pointer;
            position: absolute;
            right: -5px;
            top: -5px;
            line-height: 1em;
            svg {
                fill: $white;
                stroke: #B42500;
                transition: all 0.1s;
                &:hover {
                    fill: #B42500;
                    stroke: $white;
                }
            }
        }
    }
}

.file-name {
    white-space: pre-wrap;
    word-break: break-all;
}

.requirements-wrapper {
    flex-grow: 1;
    &:first-child {
        border-top: none;
        margin-top: 0;
        padding-top: 0;
    }
    .requirement-item {
        margin-bottom: 0;
        dt {
            margin-bottom: 0.5em;
            font-size: 12px;
            font-weight: 600;
        }
        dd {
            flex-grow: 1;
            margin-bottom: 0;
            .list-checkbox {
                li {
                    &:not(:last-child) {
                        margin-bottom: 0.5em;
                    }
                }
            }
        }
    }
}

.requirement-item {
    .form-check-input + .form-check-label  {
        font-size: 11px;
    }
    .list-checkbox {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        width: 100%;
        > li {
            padding-right: 10px;
            line-height: 1.2;
            .form-check-label {
                padding-top: 2px;
                &:before {
                    top: 0.1em;
                }
            }
        }
    }
}

.jobs-list-wrapper {
    h3 {
        font-weight: 300;
        padding: 0 0.6em;
        font-size: 38px;
        text-shadow: 0 1px 3px rgba($black, 0.16);
        flex-shrink: 0;
    }
}


.job-shortcut-card {
    margin: 24px 0;
    background-color: $white;
    border-radius: 4px;
    box-shadow: 0 1px 5px rgba($black, 0.16);
    .card-body {
        padding: 20px;
    }
    .shortcut-status {
        align-items: center;
        margin-bottom: 7px;
        dt {
            font-weight: 400;
            margin-right: auto;
            font-size: 12px;
            left: 0.05em;
        }
        dd {
            font-size: 11px;
            margin-bottom: 0;
        }
    }
    .shortcut-customer {
        margin-bottom: 10px;
        dt {
            line-height: 1.2em;
            margin-bottom: 0.3em;
        }
        dd {
            font-size: 13px;
        }
    }
    .shortcut-info {
        li {
            margin-bottom: 5px;
            &:not(:last-child) {
                margin-right: 40px;
            }
            &.editor {
                width: 100%;
                margin-right: 0;
            }
        }
    }
    .author-link {
        font-size: 100%;
    }
    .card-footer {
        padding: 0;
        background-color: transparent;
        border-top: solid 1px #ddd;
        .card-buttons {
            li {
                width: 50%;
                line-height: 1em;
                .btn-icon {
                    display: flex;
                    width: 100%;
                    height: 36px;
                    align-items: center;
                    justify-content: center;
                    svg {
                        stroke: $semi-dark-grey;
                    }
                }
                &:first-child {
                    border-right: solid 1px #ddd;
                }
            }
        }
    }
}

.remove-btn {
    position: relative;
    top: -3px;
}