//DASHBOARD
@import './variables/dashboard';

//JOB
@import './variables/job';

//REPORT
@import './variables/reports';

.form-group {
    margin-bottom: 1.5rem;
    label {
        color: $semi-dark-grey;
        margin-bottom: 0.15em;
    }
    input {
        padding-top: 0.75em;
        padding-bottom: 0.7em;
    }
}

.input-group {
    &.btn-fixed {
        position: relative;
        .input-group-append {
            width: 48px;
            position: absolute;
            right: 0;
            top: 0;
            bottom: -1px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-bottom: 2px;
            z-index: 10;
            .btn {
                background: transparent;
                box-shadow: none;
                svg {
                    width: 16px;
                }
                &:hover {
                    // background-color: $light-grey;
                    box-shadow: none !important;
                }
            }
            .form-control {
                padding-right: 55px;
            }
        }
    }
}

.text-button-icon {
    cursor: pointer;
    background: none;
    border: none;
    font-size: 14px;
    color: $orange;
    text-decoration: underline;
    .icon {
        stroke-width: 2;
        margin-right: 10px;
    }
    &:hover {
        text-decoration: none;
    }
    &:focus {
        outline: none !important;
    }
}

.controls-info-label {
    .info-label {
        padding: 0.65em 1.4em 0.6em;
        border: solid 1px $light-grey;
        border-radius: 5px;
        font-weight: 700;
        color: $semi-dark-grey;
        &:not(:last-child) {
            margin-right: 20px;
        }
    }
}

.text-link {
    &.download {
        color: $orange;
        text-decoration: underline;
        &:hover {
            text-decoration: none;
        }
    }
}

.tab-button-wrapper {
    margin-bottom: 17px;
    .nav {
        border-bottom: solid 1px $light-grey;
        .nav-link {
            font-size: 16px;
            font-weight: 300;
            color: inherit;
            padding: 0.2em 1.7em;
            position: relative;
            color: #444444;
            &:before {
                content: "";
                width: 100%;
                height: 2px;
                background-color: $primary;
                position: absolute;
                left: 0;
                bottom: -1px;
                opacity: 0;
                transition: all 0.2s ease-in;
            }
            &.active {
                font-weight: 700;
                &:before {
                    opacity: 1;
                }
            }
        }
    }
}

.tab-content-wrapper {
    margin-top: 20px;
    &+.detail-controls {
        margin-top: 20px;
    }
}

.inputs-content {
    padding: 15px 15px 15px;
}

.video-thumbnail {
    position: relative;
    .play-button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 46px;
        height: 46px;
        border-radius: 50%;
        background: $orange;
        position: absolute;
        left: calc(50% - 23px);
        top: calc(50% - 23px);
        opacity: 0.8;
        color: $white;
        cursor: pointer;
        z-index: 2;
        transition: all 0.1s ease-in;
        padding-left: 1px;
        &:hover {
            transform: scale(1.1);
            opacity: 0.9;
        }
    }
    &.played {
        .play-button {
            opacity: 0;
            visibility: hidden;
        }
    }
}

.posts-data-controls {
    li {
        &:not(:last-child) {
            margin-right: 20px;
        }
    }
    .input-search-group {
        margin-bottom: 0;
        .form-control {
            width: 375px;
        }
        .input-group-append {
            .btn {
                padding-top: 1px;
                padding-bottom: 0;
                svg {
                    width: 24px !important;
                    position: relative;
                    top: -1px;
                }
            }
        }
    }
}

table.posts-data {
    color: $semi-dark-grey;
    button {
        color: $semi-dark-grey;
    }
    th {
        border-top: none;
    }
    td {
        border-top: none;
        vertical-align: middle;
        border-bottom: solid 1px #ddd;
        max-width: 250px;
    }
    .post-time {
        font-size: 11px;
    }
    a.btn-icon {
        color: inherit;
    }
    tr:nth-child(even) {
        background-color: #f5f5f5;
    }
    .text-grey.font-weight-bold,
    .text-yellow.font-weight-bold,
    .text-orange.font-weight-bold,
    .text-green.font-weight-bold,
    .text-red.font-weight-bold {
        font-size: 11px;
    }
}

.author-link {
    color: $orange !important;
    text-decoration: underline;
    font-size: 12px;
    &:hover {
        text-decoration: none;
    }
}

.fixed-buttons {
    background-color: $white;
    padding: 15px 0;
    border-top: 1px solid #ccc;
}

.lb-dataContainer {
	position: absolute;
	left: 0;
	right: 0;
	top: -35px;
	padding-top: 0;
}

.lg-backdrop.in {
	opacity: 0.9 !important;
}

.lg-autoplay-button,
#lg-share {
	display: none;
}