// This file import all other responsive
#menu-toggle {
    display: none;
}

// Medium devices (tablets, less than 992px)
@media (max-width: 991.98px) {
    .box-analytics {
        padding: 20px 20px;
    }

    .requirement-item {
        .list-checkbox {
            width: calc(100% + 30px);
            position: relative;
            padding-left: 130px;
            li {
                width: auto !important;
                flex-grow: inherit;
                flex-basis: inherit;
                min-width: 120px;
                margin-top: 3px;
                margin-bottom: 3px;
                &:first-child:not(:last-child) {
                    min-width: 130px !important;
                    position: absolute;
                    left: 0;
                    top: 2px;
                }
            }
        }
    }
    #media-videos {
        .requirement-item {
            .list-checkbox {
                padding-left: 0;
            }
        }
    }
}

// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {
    .header {
        .container {
            max-width: 100%;
        }
        .nav-list {
            position: fixed;
            z-index: 100;
            left: 0;
            top: 56px;
            height: calc(100% - 56px);
            width: 100%;
            display: block !important;
            background-color: rgba(240,240,240,1);
            padding: 0;
            padding-top: 1px;
            opacity: 0;
            visibility: hidden;
            transition: all 0.4s;
            &.open {
                opacity: 1;
                visibility: visible;
            }
            .nav-item {
                margin: 0;
                font-size: 14px;
                .nav-link {
                    padding: 15px 15px;
                    margin-bottom: 1px;
                    background-color: $white;
                }
                &.has-pulldown {
                    .nav-pulldown {
                        position: relative;
                        left: 0;
                        top: 0;
                        width: 100%;
                        transform: none;
                        opacity: 1;
                        visibility: inherit;
                        box-shadow: none;
                        padding: 0;
                        background-color: transparent;
                        li {
                            margin-bottom: 0 !important;
                            a {
                                padding: 15px 15px;
                                display: block;
                                margin-bottom: 1px;
                                background-color: $white;
                                padding-left: 35px;
                            }
                        }
                    }
                }
                &.nav-active {
                    .nav-link {
                        &:before {
                            display: none;
                        }
                    }
                }
            }
            
        }
    }

    .container.ui-admin {
        width: 100%;
    }

    .jobs-list-wrapper {
        .block-header {
            flex-direction: column;
            h3 {
                padding: 0;
            }
        }
        .posts-data-controls {
            li {
                &:not(:last-child) {
                    margin-right: 10px;
                }
                &:first-child {
                    flex-grow: 1;
                    .form-control {
                        width: 200px;
                    }
                }
            }
            .btn {
                &:not(.btn-square) {
                    width: 140px;
                    font-size: 14px;
                    svg.mr-3 {
                        margin-right: 5px !important;
                    }
                }
                &.btn-square {
                    width: 38px;
                    height: 38px;
                }
            }
        }
    }

    #menu-toggle {
        width: 22px;
        height: 30px;
        margin: 0 0;
        transform: rotate(0deg);
        transition: .5s ease-in-out;
        cursor: pointer;
        z-index: 300;
        display: block;
        align-self: center;
        margin-left: auto;
        span {
            display: block;
            position: absolute;
            height: 2px;
            width: 100%;
            background: #666;
            border-radius: 0;
            opacity: 1;
            left: 0%;
            transform: rotate(0deg);
            transition: .25s ease-in-out;
            border-radius: 6px;
            &:nth-child(1) {
                top: 6px;
            }
            &:nth-child(2),
            &:nth-child(3) {
                top: 13px;
            }
            &:nth-child(4) {
                top: 20px;
            }
        }
        &.open {
            span {
                &:nth-child(1) {
                    top: 13px;
                    width: 0%;
                    left: 50%;
                }
                &:nth-child(2) {
                    transform: rotate(45deg);
                }
                &:nth-child(3) {
                    transform: rotate(-45deg);
                }
                &:nth-child(4) {
                    top: 13px;
                    width: 0%;
                    left: 50%;
                }
            }
        }
    }

    .box-analytics {
        margin-bottom: 20px;
        padding: 15px;
    }

    .block-job-title {
        font-size: 26px;
    }

    .alert {
        padding: 0.5rem 1rem 0.5rem;
    }

    .form-user-control {
        margin-top: 10vh;
    }

    .fixed-buttons {
        .control-button {
            .btn {
                font-size: 14px;
                width: 130px;
            }
        }
        .detail-controls {
            .controls-info-label {
                .info-label {
                    font-size: 12px;
                    &:not(:last-child) {
                        margin-right: 10px;
                    }
                }
            }
        }
    }

    .file-name {
        position: relative;
        top: -4px;
    }

    .image-editor {
        width: 84px;
    }

    .requirement-item {
        .row:first-child {
            flex-direction: column-reverse;
            .file-name {
                margin-top: 8px;
                margin-bottom: 0;
                font-size: 12px;
            }
            .progress {
                width: 150px !important;
            }
        }
    }

    .image-editor {
        .image-pane {
            .pane-header {
                .form-check-label {
                    padding-left: 17px;
                    padding-top: 2px;
                    font-size: 10px;
                }
            }
            .image-input {
                .custom-input-file-label {
                    height: 68px;
                }
            }
        }
    }

    .wrap-image-editors {
        flex-direction: column;
        border-right: solid 1px $light-grey;
        margin-right: 15px;
        .image-editor {
            border-right: none;
            margin-right: 0;
            &:first-child {
                margin-bottom: 20px;
            }
            .image-input {
                margin-bottom: 0;
            }
        }
        & + .requirements-wrapper {
            textarea {
                height: 70px;
            }
        }
    }

    .job-title-header-wrapper {
        align-items: center;
        .box-countdown {
            margin-top: 0 !important;
        }
    }

    textarea.form-control {
        font-size: 12px;
    }
}

// X-Small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
    .jobs-list-wrapper {
        h3 {
            font-size: 30px;
        }
    }

    .tab-button-wrapper {
        .nav {
            .nav-link {
                font-size: 14px;
                padding-left: 1em;
                padding-right: 1em;
            }
        }
    }

    .posts-data-controls {
        flex-wrap: wrap;
        li {
            &:first-child {
                width: 100%;
                margin-right: 0 !important;
                margin-bottom: 10px;
            }
        }
    }

    .pagination {
        justify-content: center !important;
        flex-wrap: wrap;
        .page-item {
            margin: 3px !important;
        }
    }

    .job-shortcut-card {
        .card-body {
            padding: 15px;
        }
        .shortcut-info {
            li {
                font-size: 13px;
            }
        }
    }

    .form-user-control .form-header .form-title {
        font-size: 18px;
        padding: 0.6em 1em;
    }

    .form-user-control .form-body {
        padding: 20px 15px;
    }

    .fixed-buttons {
        padding-top: 10px;
        padding-bottom: 10px;
        .control-button {
            .btn {
                font-size: 12px;
                width: 110px;
                margin-right: 10px;
                padding-left: 5px;
                padding-right: 5px;
            }
        }
        .detail-controls {
            .controls-info-label {
                .info-label {
                    font-size: 10px;
                    text-align: center;
                    padding-left: 5px;
                    padding-right: 5px;
                    line-height: 1.3em;
                    padding-top: 5px;
                    padding-bottom: 5px;
                }
            }
        }
    }

    .file-name {
        margin-bottom: 5px;
    }

    .requirement-item {
        .list-checkbox {
            padding-left: 0;
            li {
                &:first-child {
                    width: 100% !important;
                    position: static !important;
                    margin-bottom: 0;
                }
            }
        }
        textarea {
            font-size: 13px;
            padding: 6px 10px;
            height: 60px;
        }
    }

    #media-videos {
        .requirement-item {
            .list-checkbox {
                li {
                    dt {
                        input {
                            width: calc(100% - 5px) !important;
                            margin-top: 5px;
                            margin-left: 0 !important;
                            left: 0 !important;
                        }
                    }
                }
            }
        }
    }

    .job-detail-wrapper {
        .form-submit {
            margin-top: 10px !important;
            .btn {
                font-size: 14px;
                width: auto;
            }
        }
    }

    .job-title-header-wrapper {
        margin-top: 20px;
        margin-bottom: 20px;
        h3 {
            font-size: 18px;
            flex-shrink: 0;
            margin-right: 15px;
        }
        .box-countdown {
            margin-top: 0 !important;
            .countdown-title {
                dt {
                    font-size: 10px;
                }
                dd {
                    font-size: 10px;
                }
            }
            .countdown-time {
                dt {
                    font: 12px;
                }
                dd {
                    font-size: 16px;
                }
            }
        }
    }

    .custom-input-file + .custom-input-file-label, .dropzone {
        font-size: 12px;
    }
}

@media (max-width: 414px) {
    .form-user-control .input-group .input-group-prepend {
        width: 34px;
    }

    .form-user-control .input-group .input-group-text {
        width: 30px;
        padding-right: 0;
        padding-left: 0;
    }

    .form-user-control .input-group .input-group-submit .btn-submit {
        width: 95px;
        font-size: 14px;
    }

    .custom-input-file + .custom-input-file-label, .dropzone {
        height: 100px;
    }

    .dropzone-direction {
        font-size: 12px;
    }
    .detail-group-controls {
        margin-top: 30px;
    }
    .inputs-content {
        padding: 10px;
    }
    .requirement-item {
        .list-checkbox {
            li {
                min-width: 90px;
            }
        }
        .row:first-child {
            .progress {
                width: 100px !important;
                font-size: 9px;
                margin-right: 20px !important;
            }
        }
    }
    .remove-btn {
        font-size: 12px;
        top: -2px
    }

    .job-detail-wrapper {
        .form-submit {
            .btn {
                width: 100% !important;
                margin: 0 0 10px;
            }
        }
    }
}