.box-analytics {
    border: solid 1px $light-grey;
    padding: 22px 30px;
    border-radius: 5px;
    margin-bottom: 30px;
    color: $dark-grey;
    background: $white;
    .analytics-number {
        font-size: 28px;
        font-weight: 600;
        color: $primary;
        margin-bottom: 0.5em;
    }
    .analytics-title {
        margin-bottom: 15px;
        &:last-child {
            margin-bottom: 0;
        }
        dt {
            font-size: 18px;
            line-height: 1em;
            font-weight: 600;
        }
        dd {
            font-size: 12px;
            margin-bottom: 0;
        }
        &.large {
            dt {
                font-size: 24px;
                margin-bottom: 0.2em;
            }
        }
    }
    &.ranking {
        padding-top: 25px;
        padding-bottom: 25px;
    }
    .ranking-list {
        &:not(:first-child) {
            margin-top: 30px;
        }
        .ranking-item {
            display: flex;
            &:not(:last-child) {
                margin-bottom: 18px;
            }
            .ranking-number {
                width: 36px;
                height: 36px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 50%;
                background-color: #FFF4D5;
                margin-right: 10px;
                flex-shrink: 0;
                font-weight: 600;
            }
            .ranking-text {
                & > span {
                    display: block;
                    font-size: 12px;
                    line-height: 1.5em;
                    &.ranking-title {
                        font-size: 14px;
                        font-weight: 600;
                    }
                }
            }
            &:first-child {
                .ranking-number {
                    background-color: $primary;
                }
            }
        }
    }
    .list-summary {
        .summary-item {
            &:not(:last-child) {
                margin-bottom: 10px;
            }
            .num {
                font-weight: 600;
                margin-left: 7px;
            }
        }
    }
}