// FOOTER & HEADER
.header {
    background: $white;
    &.fixed-header {
        position: fixed;
        left: 0;
        top: 0;
        background: $white;
        box-shadow: 0 1px 3px rgba($black, 0.16);
        width: 100%;
        z-index: 100;
        &+.main {
            margin-top: 56px;
            padding-top: 1px;
        }
    }
}

.main-nav {
    height: 56px;
    align-items: center;
    .nav-list {
        .nav-item {
            margin: 0 9px;
            font-size: 12px;
            .nav-link {
                padding: 18px 5px;
                color: $semi-dark-grey;
                text-transform: uppercase;
                position: relative;
                transition: all 0.1s ease-in;
                &[href]:hover {
                    color: $primary;
                }
            }
            &.nav-active {
                .nav-link {
                    &:before {
                        content: "";
                        width: 100%;
                        height: 2px;
                        border-radius: 2px;
                        background-color: $primary;
                        position: absolute;
                        left: 0;
                        bottom: 0px;
                    }
                }
            }
            &.has-pulldown {
                position: relative;
                .nav-pulldown {
                    position: absolute;
                    left: 50%;
                    top: 100%;
                    transform: translateX(-50%);
                    background-color: $white;
                    box-shadow: 0 4px 3px rgba($black, 0.16);
                    padding: 20px 25px;
                    min-width: 200px;
                    // transition: all 0.2s ease-in;
                    opacity: 0;
                    visibility: hidden;
                    .nav-pulldown-item {
                        font-size: 14px;
                        &:not(:last-child) {
                            margin-bottom: 1.4em;
                        }
                        a {
                            white-space: nowrap;
                            word-break: keep-all;
                            color: $semi-dark-grey;
                            &:hover {
                                color: $primary;
                                text-decoration: none;
                            }
                        }
                    }
                }
                &:hover {
                    .nav-pulldown {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }
        }
    }
}

.admin-page {
    .main-nav {
        .nav-item {
            .nav-link {
                padding-left: 1.3em;
                padding-right: 1.3em;
            }
        }
    }
}

.footer {
    &.dark {
        background: $dark-grey;
        address {
            margin-bottom: 0;
        }
    }
    .f-content {
        align-items: center;
        height: 84px;
    }
}

// COMPONENTS
.form-user-control {
    width: 480px;
    max-width: 100%;
    border-radius: 5px;
    background-color: rgba($white, 0.9);
    box-shadow: 0 3px 6px rgba($black, 0.16);
    margin: 165px auto 0;
    overflow: hidden;
    .form-header {
        position: relative;
        border-bottom: solid 1px $light-grey;
        &:before {
            content: "";
            width: 120px;
            height: 2px;
            background: $primary;
            position: absolute;
            left: 0;
            bottom: -1px;
            border-radius: 2px;
        }
        .form-title {
            font-size: 20px;
            padding: 0.8em 1.5em;
        }
    }
    .form-body {
        padding: 30px 30px;
    }
    .form-footer {
        border-top: solid 1px $light-grey;
        padding: 10px 0 11px;
    }
    .input-group {
        .input-group-prepend {
            width: 44px;
            &~.text-error {
                margin-left: 44px;
                &:last-child {
                    margin-bottom: -3px;
                }
            }
        }
        .input-group-text {
            background: none;
            border: none;
            padding-left: 8px;
            &.password-icon {
                width: 40px;
                margin: 0 auto;
            }
        }
        .input-group-submit {
            flex-grow: 1;
            align-items: center;
            .btn-submit {
                margin-left: auto;
            }
        }
    }
}

h3 {
    font-size: 30px;
    color: $dark-grey;
    font-weight: 700;
    margin-bottom: 0.4em;
}

h6 {
    color: #666666;
    font-weight: 700;
}

a {
    color: $primary;
    &:hover {
        color: $primary;
    }
}

.banner-daily {
    background-color: $primary;
    .banner-content-wrapper {
        height: 115px;
        align-items: center;
        margin-bottom: 0;
        justify-content: space-between;
        dt {
            font-size: 24px;
            font-weight: 700;
            color: $white;
        }
        dd {
            margin-bottom: 0;
        }
    }
}

.form-wrapper {
    width: 756px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    textarea {
        min-height: 110px;
    }
}

.photos-slider {
    margin-bottom: 0 !important;
    .slick-dots {
        bottom: 0;
        li {
            margin: 0 0 5px;
            button {
                &:before {
                    color: $white;
                    font-size: 10px;
                }
            }
            &.slick-active {
                button {
                    &:before {
                        color: $white;
                    }
                }
            }
        }
    }
}

.box-countdown {
    display: flex;
    flex-wrap: wrap;
    width: 300px;
    height: 56px;
    align-items: center;
    justify-content: space-between;
    border-radius: 4px;
    box-shadow: 0 4px 12px rgba($black, 0.16);
    color: $white;
    background: #064b8e;
    background: -moz-linear-gradient(left, #064b8e 0%, #0a63be 100%);
    background: -webkit-linear-gradient(left, #064b8e 0%,#0a63be 100%);
    background: linear-gradient(to right, #064b8e 0%,#0a63be 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#064b8e', endColorstr='#0a63be',GradientType=1 );
    padding: 3px 10px 5px;
    dl {
        margin-bottom: 0;
        dt {
            font-size: 12px;
            font-weight: 400;
        }
        dd {
            margin-bottom: 0;
            font-size: 12px;
            font-weight: 500;
            line-height: 1.1em;
        }
        &.countdown-title {
            color: #85BAEF;
        }
        &.countdown-time {
            text-align: right;
            dd {
                font-size: 22px;
                font-weight: 700;
            }
        }
    }
}

// PAGES
body.oneview {
    min-height: calc(100vh);
    background: url('../assets/img/images/bg-img-1.jpg') center top / cover no-repeat;
    display: flex;
    flex-direction: column;
    .footer {
        margin-top: auto;
        padding-top: 1rem;
    }
}

.block-form-contact {
    padding: 49px 30px 64px;
    border-radius: 5px;
    box-shadow: 0 1px 3px rgba($black, 0.1);
}

// LOADING SCREEN
#loading-screen {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1100;
    background: rgba($white, 0.8);
    .spinner {
        width: 40px;
        height: 40px;
        background-color: $orange;
        -webkit-animation: sk-rotateplane 1.2s infinite ease-in-out;
        animation: sk-rotateplane 1.2s infinite ease-in-out;
        position: absolute;
        left: calc(50% - 20px);
        top: calc(50% - 20px);
    }
}

@-webkit-keyframes sk-rotateplane {
    0% { -webkit-transform: perspective(120px) }
    50% { -webkit-transform: perspective(120px) rotateY(180deg) }
    100% { -webkit-transform: perspective(120px) rotateY(180deg)  rotateX(180deg) }
}

@keyframes sk-rotateplane {
    0% { 
        transform: perspective(120px) rotateX(0deg) rotateY(0deg);
        -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg) 
    } 50% { 
        transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
        -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg) 
    } 100% { 
        transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
        -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    }
}