.visual-image {
    position: relative;
    padding-bottom: 56.3%;
    iframe {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
}

.welcome-content-wrapper {
    background: $light-yelow;
    .text-secondary {
        font-size: 13px;
    }
    .box-button {
        margin-top: 3em;
    }
}

.our-skill {
    .skill-content-wrapper {
        .skill-card {
            padding: 60px 10px 30px;
            height: 312px;
            box-shadow: 0 1px 3px rgba($black, 0.1);
            border-radius: 5px;
            background: $white;
            .card-icon {
                display: flex;
                width: 80px;
                height: 80px;
                margin: 0 auto 3px;
                color: #fff;
                justify-content: center;
                align-items: center;
                background: #F9B000;
                border-radius: 50%;
                flex-shrink: 0;
                font-size: 26px;
            }
            .card-title {
                font-weight: 400;
                line-height: 1.35em;
                height: 2.7em;
                margin-bottom: 0.75em !important;
            }
            .text-secondary {
                font-weight: 400;
                font-size: 12px;
                color: $semi-dark-grey !important;
            }
        }
    }
}

.more-service {
    .more-service-item {
        &:not(:last-child) {
            margin-bottom: 36px;
        }
    }
}

// Large devices (desktops, less than 1200px)
@media (max-width: 1199.98px) {
    .banner-daily {
        .container {
            max-width: 100%;
        }
        .banner-content-wrapper {
            align-items: center;
            dt {
                line-height: 1.3em;
                margin-right: 30px;
            }
        }
    }
}

// Medium devices (tablets, less than 992px)
@media (max-width: 991.98px) {
    img {
        max-width: 100%;
    }

    .container.px-0 {
        .row {
            margin-left: 0;
            margin-right: 0;
            padding-left: 15px !important;
            padding-right: 15px !important;
        }
    }
}

// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {
    .main {
        & > .pb-5 {
            padding-bottom: 30px !important;
        }

        & > .py-5 {
            padding-top: 30px !important;
            padding-bottom: 30px !important;
        }
    }

    .welcome-content-wrapper {
        .col-text {
            padding-top: 30px !important;
            padding-left: 0 !important;
        }
        .box-button {
            margin-top: 1rem;
        }
    }

    h3 {
        font-size: 24px;
    }

    .service-content-wrapper {
        margin-top: 0 !important;
    }

    .our-service {
        .box-button {
            margin-top: 20px !important;
        }
    }

    .our-skill {
        .skill-content-wrapper {
            margin-top: 30px !important;
            .skill-card {
                padding-top: 40px;
                height: auto;
            }
        }
    }

    .more-service {
        h3 {
            margin-bottom: 30px !important;
        }
    }

    .more-service {
        .more-service-content-wrapper {
            .more-service-item {
                background-color: #F9F9F9 !important;
                padding: 15px !important;
                .item-content {
                    margin-top: 20px;
                    h3 {
                        margin-bottom: 10px !important;
                    }
                }
                .item-media {
                    iframe {
                        max-height: 50vw;
                    }
                }
            }
        }
    }

    .product-introduce {
        .introduce-image {
            img {
                // transform: translateX(5%);
            }
        }
    }

    .block-form-contact {
        padding: 30px 15px;
        .row.mb-4 {
            margin-bottom: 0 !important;
            .form-control {
                margin-bottom: 15px;
            }
        }
        .text-secondary.mb-5 {
            margin-bottom: 30px !important;
        }
    }

    .footer {
        &.dark {
            .container {
                max-width: 100%;
            }
        }
    }
}

// X-Small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
    .our-skill {
        .skill-content-wrapper {
            .skill-card {
                padding: 30px 20px 10px;
                .card-body {
                    padding-left: 0;
                    padding-right: 0;
                }
            }
        }
    }

    .banner-daily {
        .banner-content-wrapper {
            flex-direction: column;
            padding: 15px 0;
            height: auto;
            dt {
                font-size: 18px;
                text-align: center;
                margin-bottom: 10px;
                margin-right: 0;
            }
        }
    }

    .footer {
        &.dark {
            .f-content {
                height: auto;
                flex-direction: column;
                padding: 15px 0;
                address {
                    margin: 15px 0 0 0 !important;
                    text-align: center;
                    line-height: 1.3em;
                }
            }
        }
    }
}