//** ------ {{ color and cusotmiaztion variables }} ------------------------------------
//** application color table

$white: #FFFFFF;
$light-grey: #bbb;
$grey: #666;
$dark-grey: #333;
$blue: #004789;
$primary: #F9B000;
$orange: #D16800;
$light-yelow: #FFF9E9;
$black: #000000;
$semi-dark-grey: #444;
// Font

@function str-replace($string, $search, $replace: "") {
	$index: str-index($string, $search);

	@if $index {
		@return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
	}

	@return $string;
}

@mixin font-face($name, $path, $weight: null, $style: null, $exts: eot woff2 woff ttf svg) {
	$src: null;

	$extmods: (
		eot: "?",
		svg: "#" + str-replace($name, " ", "_")
	);

	$formats: (
		otf: "opentype",
		ttf: "truetype"
	);

	@each $ext in $exts {
		$extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);
		$format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);
		$src: append($src, url(quote($path + "." + $extmod)) format(quote($format)), comma);
	}

	@font-face {
		font-family: quote($name);
		font-style: $style;
		font-weight: $weight;
		src: $src;
	}
}

// @include font-face('Arimo-Bold', '../assets/fonts/Arimo-Bold');
// @include font-face('Arimo-Regular', '../assets/fonts/Arimo-Regular');

@mixin Arimo-Bold {
  font-family: 'Arimo-Bold';
}
@mixin Arimo-Regular {
  font-family: 'Arimo-Regular';
}
@mixin Muli-Bold {
	font-family: 'Muli', sans-serif;
	font-weight: 700;
}

@mixin Muli-Regular {
  font-family: 'Muli', sans-serif;
	font-weight: 400;
}
@mixin Muli-Black {
	font-family: 'Muli-SemiBold';
	font-weight: 900;
}

@mixin clearfix { 
  &:after {
    content: ".";
    visibility: hidden;
    display: block; 
    clear: both;
    height: 0;
  }
}



/** --------------------------------------- **/
/**
 * SET UP WHOLE SITE
 */
body {
	background: $white;
	color: $dark-grey;
	position: relative;
	margin: 0px;
	line-height: 1.6;
	font-size: 14px;
	font-family: 'Poppins', sans-serif;
	font-weight: 400;
	&.admin-page {
		background-color: #efefef;
	}
}

h1, h2, h3, h4, h5, h6, p {
	margin: 0;
	padding: 0;
}

ul { 
	margin: 0; 
	padding: 0; 
	li {
		list-style: none;
	}
}

/** --------------------------------------- **/
/**
 * CUSTOM COMPONENTS
 */

.text-yellow {
	color: $primary !important;
}

svg.feather {
	&:not(.custom-stroke) {
		stroke-width: 1;
	}
}

::placeholder {
	color: $light-grey !important;
	font-weight: 300;
}

.form-control {
	font-size: 14px;
	border-color: $light-grey;
	border-radius: 4px !important;
	padding-top: 0.8em;
	padding-bottom: 0.8em;
	height: auto;
	left: 0.05em;
	&:focus {
		border-color: rgba($primary, 0.3);
		box-shadow: 0 0 0 0.2rem rgba($primary, 0.25);
	}
	&.custom-select {
		padding-bottom: 0.7em;
	}
	&.d-inline {
		padding-top: 0.25em;
		padding-bottom: 0.25em;
	}
}

textarea.form-control {
	resize: none;
}

.input-group {
	&:not(:last-child) {
		margin-bottom: 20px;
	}
}

.btn {
	width: 188px;
	font-size: 16px;
	font-weight: 500;
	padding-top: 0.6em;
	padding-bottom: 0.5em;
	box-shadow: 0 1px 3px rgba($black, 0.16);
	outline: none !important;
	transition: all 0.2s ease-in;
	&:hover,
	&:focus,
	&:active {
		box-shadow: 0px 3px 14px rgba($black, 0.16) !important;
	}
	&.btn-primary {
		background-color: $primary;
		border-color: $primary;
		&:hover,
		&:focus,
		&:active {
			background-color: $primary !important;
			border-color: $primary !important;
		}
	}
	&.btn-light {
		border: solid 2px $dark-grey;
		color: $dark-grey;
		&:hover {
			background-color: $white;
		}
	}
	&.btn-outline-primary {
		border-color: $primary;
		color: $primary;
		background-color: $white;
		&:hover,
		&:focus,
		&:active {
			border-color: $primary !important;
			color: $primary !important;
			background-color: $white !important;
		}
	}
	&.btn-square {
		padding: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 46px;
		height: 44px;
		.feather {
			top: 0;
		}
	}
	.feather {
		position: relative;
		top: -1px;
	}
}

.form-text-link {
	color: $grey;
	font-size: 11px;
	&:hover {
		color: $primary;
	}
}

.text-link-underline {
	font-size: 12px;
	color: $dark-grey !important;
	text-decoration: underline;
	&:hover {
		text-decoration: none;
	}
}

.text-info-small {
	font-size: 12px;
	color: #008015;
}

.text-error {
	color: #B42500;
	font-size: 12px;
	margin-top: 7px;
}

.text-secondary {
	color: #959595 !important;
	line-height: 1.4em;
	&:not(:last-child) {
		margin-bottom: 1.4em;
	}
}

.btn-icon-image {
    font-size: 14px;
    color: #D16800 !important;
    text-decoration: none;
    padding-left: 22px;
    background: url(../assets/img/images/icon-thumbnail-1.png) left center/13.5px no-repeat;
    letter-spacing: 0;
}

.bg-light {
	background-color: #F9F9F9 !important;
}

.text-green {
	color: #008015;
}

.text-red {
	color: #B42500;
}

.text-orange {
	color: #D16800;
}

.text-grey {
	color: #444444;
}

.alert {
	padding: 1.2rem 1.8rem 1.05rem;
	&.alert-success {
		color: #008015;
		border-color: #008015;
		background-color: #EFFFF2;
	}
}

.form-check-input {
	display: none;
}

.form-check-input + .form-check-label {
	cursor: pointer;
	padding-left: 22px;
	position: relative;
	&:before {
		content: "";
		width: 14px;
		height: 14px;
		border-radius: 2px;
		border: solid 1px #ddd;
		background: transparent;
		position: absolute;
		left: 0;
		top: 0.16em;
	}
	&:after {
		content: "";
		width: 9px;
		height: 5px;
		border-left: solid 2px $white;
		border-bottom: solid 2px $white;
		position: absolute;
		left: 0.3em;
    	top: 0.4em;
		transform: rotate(-45deg);
		visibility: visible;
	}
	&.small-label {
		font-size: 12px;
		padding-top: 0.1em;
		// &:after {
		// 	left: 3px;
		// 	top: 6px;
		// }
	}
}

.form-check-input:checked + .form-check-label {
	&:before {
		border-color: $primary;
		background: $primary;
	}
	&:after {
		visibility: visible;
	}
}

.custom-input-file {
	display: none;
}

.custom-input-file + .custom-input-file-label,
.dropzone {
	display: flex;
    height: 130px;
    border-radius: 5px;
    background-color: #F9F9F9;
    border: dashed 1px #F9B000;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s ease-in;
    margin-bottom: 5px;
    flex-wrap: wrap;
    overflow: hidden;
    padding: 10px 10px;
	.dropzone-direction {
		margin: 5px;
		width: 100%;
		align-self: center;
	}
	&:hover {
		background-color: rgba($primary, 0.1);
	}
	.dz-preview {
		width: 60px;
		height: 60px;
		margin: 2px;
		min-height: unset;
		.dz-image {
			width: 100% !important;
			height: 100% !important;
			border-radius: 5px;
			img {
				width: 100% !important;
				height: 100% !important;
				object-fit: cover;
			}
		}
		.dz-detail {
			padding: 5px;
		}
		.dz-progress {
			width: 70px;
			margin-left: -35px;
		}
	}
}

.custom-input-file-label + .text-secondary,
.dropzone + .text-secondary {
	font-size: 12px;
}

table {
	.action-cell {
		cursor: pointer;
	}
}

.btn-icon {
	border: none !important;
	background: none;
	padding: 5px 5px;
	outline: none !important;
	cursor: pointer;
	transition: all 0.1s ease-in;
	.feather {
		transition: all 0.1s ease-in;
	}
	&:hover {
		.feather {
			stroke: $orange;
		}
	}
}

.pagination {
	&.justify-content-end {
		li:not(:last-child) {
			margin-right: 9px;
		}
	}
	.page-link {
		border-radius: 5px;
		color: $grey;
		transition: all 0.1s ease-in;
		display: flex;
		min-width: 35px;
		height: 35px;
		padding: 0px 5px;
		justify-content: center;
		align-items: center;
		&:hover {
			border-color: $primary;
			color: $primary;
			background: $white;
		}
	}
	.page-item {
		&.active {
			.page-link {
				background: $primary;
				color: $white;
				border-color: $primary;
				box-shadow: 0 1px 3px rgba($black, 0.16);
			}
		}
		&.disabled {
			.page-link {
				background: #f1f2f3;
				color: #ccc;
			}
		}
	}
}

video {
	outline: none;
}

.container.ui-admin {
	max-width: 1440px;
	width: 96%;
}